<template>
    <div>
        <!-- Table All PO -->
        <vx-card>
        <vs-table stripe search pagination notSpacer max-items="10" :data="po">
            <template slot="header" class="mb-5">
                 <h1>Data Pesanan Pembelian</h1>
            </template>

            <template slot="thead">
                <vs-th sort-key="tanggal" width="120px" class="bg-primary text-white">
                <p class="w-full">Tanggal</p>
                </vs-th>
                <vs-th sort-key="stockpoint" class="bg-primary text-white">
                <p class="w-full">Stock Point</p>
                </vs-th>
                <vs-th sort-key="barang" class="bg-primary text-white">
                <p class="w-full">Barang</p>
                </vs-th>
                <vs-th sort-key="qty" class="bg-primary text-white">
                <p class="w-full">Qty</p>
                </vs-th>
                <vs-th sort-key="supplier" class="bg-primary text-white">
                <p class="w-full">Supplier</p>
                </vs-th>
                <vs-th width="10px" sort-key="aksi" class="bg-primary text-white  text-center">
                Aksi
                </vs-th>
            </template>

            <template slot-scope="{data}">
                <vs-tr :key="items.kode" v-for="items in data">
                    <vs-td :data="items.tanggal">
                        {{items.tanggal}}
                    </vs-td>

                    <vs-td :data="items.stockpoint">
                        {{items.stockpoint}}

                    </vs-td>

                    <vs-td :data="items.barang">
                        {{items.barang}}
                    </vs-td>

                    <vs-td :data="items.qty">
                        {{items.qty}}
                    </vs-td>

                    <vs-td :data="items.kode_supplier">
                        <div v-if="items.kode_supplier == 0">
                            -
                        </div>
                        <div v-else>{{items.nama_supplier }}</div>
                    </vs-td>

                    <vs-td :data="items.aksi" class="text-center">
                        <vx-tooltip text="Ubah Pesanan">
                        <vs-button @click="editData(items.kode, items.qty, items.kode_supplier,)" class="mr-1" color="warning" type="filled" size="small" icon="create" style="display:inline"></vs-button>
                        </vx-tooltip>
                    </vs-td>
                </vs-tr>
            </template>
        </vs-table>
        </vx-card>

        <!-- Ubah PO -->
        <vs-popup class="holamundo"  title="Ubah Pesanan Pembelian" :active.sync="Edit">
                <div class="vx-row mb-6">
                    <div class="vx-col">
                    <small class="ml-1">Qty</small><br>
                    <vs-input-number min="1" v-validate="'required'" name="form.qty" data-vv-validate-on="blur" v-model="form.qty" />
                    <span class="text-danger text-sm">{{ errors.first('form.qty') }}</span>
                    </div>
                </div>
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <small class="ml-1">Supplier</small><br>
                    <v-select :clearable="false" :placeholder="'-- Pilih Supplier --'" name="form.kode_supplier" v-validate="'required'" data-vv-validate-on="blur" :reduce="supplier => supplier.kode" label="nama" :options="supplier" v-model="form.kode_supplier"  ></v-select>
                    <span class="text-danger text-sm">{{ errors.first('form.kode_supplier') }}</span>
                    </div>
                </div>
                <div class="vx-row">
                    <div class="vx-col w-full text-right">
                    <vs-button color="success" :disabled="!validateForm" class="mr-3 mb-2" @click="updateData(form.kode)">Simpan</vs-button>
                    </div>
                </div>
        </vs-popup>
    </div>
</template>

<script>
export default {
    data() {
        return {
            publicPath: process.env.BASE_URL,
            addNew:false,
            Edit:false,
            po:[],
            supplier:[],
            form: {
                qty:'',
                kode_supplier: null,
            }

        }
    },

    computed: {
        validateForm() {
            return !this.errors.any() 
            && this.form.qty != '' 
            && this.form.kode_supplier != '';
        },
    },

    created() {
       this.loadData();
       this.loadSupplier();
    },

    methods:{
        loadData(){
            this.$http.get('agen/web/po')
            .then(response => {
                this.po = response.data.message
                // console.log(response);
            })
            .catch(err=>{
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
        },

        loadSupplier(){
            this.$http.get('agen/web/supplier')
            .then(response => {
                this.supplier = response.data.message
                // console.log(response);
            })
            .catch(err=>{
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
        },

        editData(kode, qty, kode_supplier){
            this.Edit = true
            this.form.kode = kode,
            this.form.qty = qty,
            this.form.kode_supplier = kode_supplier
                // console.log(response.data.message[0].kode);
        },

        updateData(kode){
            this.$vs.loading();
            const updtData = {
                kode: kode,
                qty: this.form.qty,
                kode_supplier: this.form.kode_supplier
            }

            this.$http.post('agen/web/po/update', updtData)
            .then(response => {
                if(response.data.status == "SUKSES"){
                    this.form.qty = '',
                    this.kode_supplier = null,
                    this.Edit = false

                    this.$swal.fire({
                    title: 'Success',
                    text: response.data.message,
                    icon: 'success',

                    }).then(() => {
                        this.loadData();
                    })
                }else {
                    this.$swal.fire(
                        'Gagal !',
                        response.data.message,
                        'error'
                    )
                }
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()})
        }
    }

}
</script>

<style lang="stylus">

    .swal2-container 
        z-index: 100000;
        
</style>